* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  border: none;
}

body {
  font-family: sans-serif;
}

ul {
  list-style-type: none;
}

a, a:link, a:visited {
  color: black;
  text-decoration: none;
}

input {
  outline: none;
  padding: 10px 0;
  border: none;
  border-bottom: 1px solid rgb(5, 136, 212);
  text-indent: 10px;
}

button {
  padding: 5px;
  border: 1px solid black;
  cursor: pointer;
  outline: none;
}
