.profile {
  box-shadow: 0 0 5px black;
  width: 300px;
  padding: 10px;
  margin: 0 auto;
  text-align: center;
}

.profile:hover {
  box-shadow: 0 0 10px black;
}

.profile__followings,
.profile__followers,
.profile__username {
  margin: 10px 0;
  font-weight: bold;
}

.profile__table {
  width: 100%;
}

.profile__table td {
  width: 50%;
  padding: 10px 0;
}
