.login {
  box-shadow: 0 0 5px black;
  width: 250px;
  padding: 20px;
  text-align: center;
  margin: 0 auto;
}

.login:hover {
  box-shadow: 0 0 10px black;
}

.login__field {
  width: 100%;
}

.login__submit {
  margin-top: 20px;
  font-weight: bold;
  border: 2px solid black;
  padding: 5px 10px;
  background-color: transparent;
}
