.alert {
  border: 3px solid rgb(194, 5, 5);
  position: fixed;
  height: 130px;
  width: 300px;
  top: -130px;
  left: 50%;
  margin-left: -150px;
  background-color: white;
  animation-duration: .2s;
  animation-fill-mode: forwards;
  animation-timing-function: linear;
  display: flex;
  align-items: center;
  justify-content: center;
}

.alert__close {
  position: absolute;
  bottom: 10px;
  right: 10px;
  font-weight: bold;
  width: 100px;
  border: none;
  background-color: rgb(6, 206, 33);
  color: white;
}

.animate-alert-down {
  animation-name: alert-down;
}

.animate-alert-up {
  animation-name: alert-up;
}

@keyframes alert-down {
  from {
    top: -130px;
  }
  to {
    top: 100px;
  }
}

@keyframes alert-up {
  from {
    top: 100px;
  }
  to {
    top: -130px;
  }
}
