.register {
  width: 250px;
  padding: 20px;
  box-shadow: 0 0 5px black;
  text-align: center;
  margin: 0 auto;
  border-radius: 5px;
}

.register:hover {
  box-shadow: 0 0 10px black;
}

.register__field {
  width: 100%;
}

.register__submit {
  margin-top: 20px;
  font-weight: bold;
  border: 2px solid black;
  padding: 5px 10px;
  background-color: transparent;
}
