.users {
  text-align: center;
  width: 200px;
  margin: 0 auto;
}

.users__user {
  padding: 20px;
  box-shadow: 0 0 5px black;
  margin: 20px 0;
}

.users__user:hover {
  box-shadow: 0 0 10px black;
}

.user__follow,
.user__unfollow {
  width: 70px;
  height: 30px;
  margin-top: 10px;
  border-radius: 5px;
  border: none;
  color: white;
  font-size: 16px;
}

.user__follow {
  background-color: green;
}

.user__unfollow {
  background-color: rgb(248, 2, 2);
}
