.home {
  text-align: center;
}

.home__label {
  /* border: 1px solid yellow; */
  margin: 20px 0;
}

.posts {
  /* border: 3px solid blue; */
  width: 400px;
  margin: 0 auto;
  text-align: center;
}

.posts__post {
  margin: 20px;
  padding: 10px;
  box-shadow: 0 0 5px black;
  text-align: left;
}

.posts__post:hover {
  box-shadow: 0 0 10px black;
}

.post__likes {
  color: rgb(252, 0, 0);
  font-size: 26px;
  vertical-align: middle;
  text-shadow: 0 0 5px red;
}

.post__title {
  color: rgb(10, 14, 224);
}

.post__body {
  margin: 20px 0;
}

.post__open-comments-btn,
.post__close-comments-btn {
  color: rgb(79, 79, 194);
  cursor: pointer;
  margin-top: 20px;
}

.post__comments {
  margin-top: 20px;
  /* border: 1px solid black; */
}

.comments__comment {

}

.comment__author {
  /* color: rgb(5, 218, 189); */
}

.comment__body {
  color: rgb(0, 0, 0);
  text-indent: 10px;
}

.post__add-btn {
  font-weight: bold;
  color: turquoise;
  background-color: transparent;
  border: 3px solid turquoise;
  border-radius: 5px;
}
